* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

video {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  border: none;
}

.App {
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  /*background-color: gray;*/
  overflow: hidden;
  z-index: 2;
  font-family: "Courier New", Courier, monospace;
}

.blob {
  background-color: white;
  border-radius: 15px;
  padding: 10px;
  border: 1px black solid;
  box-shadow: 10px 5px 5px;
}

header {
  font-size: 69px;
  white-space: nowrap;
  position: absolute;
  top: 220px;
  left: 50%;
  transform: translateX(-50%);
}

button {
  border-radius: 50px;
  font-family: "Courier New", Courier, monospace;
  background: rgb(131, 58, 180);
  /*background: linear-gradient(209deg, rgba(131,58,180,1) 0%, rgba(253,29,29,0.586374695863747) 50%, rgba(252,176,69,1) 100%);*/
  border: none;
  height: 35px;
  width: 95px;
  transition: all 0.4s;
}

button:hover {
  background: rgb(128, 123, 209);
  /*background: radial-gradient(circle, rgba(128,123,209,1) 0%, rgba(15,207,129,1) 52%, rgba(218,193,17,1) 100%);
  */
}

.selectors {
  position: absolute;
  top: 37%;
  font-size: 35px;
  display: grid;
  grid-template-columns: auto auto;
}

#BreakSelector {
  left: 33%;
  transform: translateX(-50%);
}
#BreakSelector #break-label {
  grid-column: 1/span 2;
}
#BreakSelector #break-length {
  margin: 0 auto;
  grid-column: 1/span 2;
}

#SessionSelector {
  left: 66%;
  transform: translateX(-50%);
}
#SessionSelector #session-label {
  grid-column: 1/span 2;
}
#SessionSelector #session-length {
  margin: 0 auto;
  grid-column: 1/span 2;
}

#mainDisplay {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
  width: 25%;
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 55px;
  border: 2px solid black;
}

button:hover {
  animation: pulse 1s;
  box-shadow: 0 0 0 2em transparent;
}